<template>
  <div class="text_components">
    <div class="container-text_components">
      <div class="text_components-top_all">
        <div @click.stop="back()" class="back">
          <img alt src="../../assets/image/联合42.png" />
        </div>
        <div class="text_components-top-header">
          <div class="text_components-but-all">
            <div
              :class="{text_hit:true , text_hit_change:isChange == index}"
              :key="index"
              @click="go(index)"
              v-for="(item,index) in all"
            >
              <div>{{item.title}}</div>
            </div>
          </div>
        </div>
        <router-view></router-view>
      </div>
      <!--题目内容-->
      <div class="swiper-container body">
        <div class="swiper-wrapper">
          <div :key="index" class="activeBox swiper-slide" v-for="(item, index) in activeItems">
            <div :class="index+'a'" v-if="item">
              <div
                style="position: absolute;top: 0;height:calc(100vh - 0.98rem - 0.94rem);width:100%;overflow-y: scroll;"
              >
                <div class="fontstyle">
                  <div class="typePhoto" v-if="item.exerciseType == '0101'">单选题</div>
                  <div class="typePhoto" v-if="item.exerciseType == '0102'">多选题</div>
                  <div class="typePhoto" v-if="item.exerciseType == '0103'">判断题</div>
                  <div class="typePhoto" v-if="item.exerciseType == '0104'">不定项</div>
                  <div class="typePhoto" v-if="item.exerciseType == '0105'">填空题</div>
                  <div class="text_mian_title" v-show="item.stem != '' && item.stem != null ">
                    &nbsp;&nbsp;
                    {{item.exerciseNumber}}.{{item.stem}}
                  </div>
                </div>
                <!-- 选项 -->
                <div
                  :key="index"
                  class="swiper-slide options"
                  v-for="(i,index) in item.exerciseOptions"
                >
                  <div class="text-main-options" v-if="item.exerciseType !== '0105'">
                    <div
                      :class="selectClass(index , i,item)"
                      :id="i.exerciseId+'_'+String.fromCharCode(65 + index)"
                      @click="select(item,i)"
                      style="width: 0.42rem;height: 0.42rem;background-size:100% 100%;"
                    ></div>&nbsp;&nbsp;&nbsp;&nbsp;
                    <div style="width: 88%;font-size:0.36rem;line-height: 0.48rem;">
                      <span style='word-break: break-word;'
                        :class="selectClassSpan(index , i,item)"
                        :id=" String.fromCharCode(65 + index) + '_' + i.exerciseId "
                        @click="select(item,i)"
                      >{{i.exerciseOption}}</span>
                    </div>
                  </div>
                  <div class="text-main-options" v-if="item.exerciseType === '0105'">
                    <div
                      :id="i.exerciseId+'_'+String.fromCharCode(65 + index)"
                      style="width: 0.42rem;height: 0.42rem;background-size:100% 100%;"
                    ><span style="color:#2D2D2F;">(</span><span style="color:#2D2D2F;font-size:0.3rem;">{{index + 1}}</span><span style="color:#2D2D2F;">)</span></div>&nbsp;&nbsp;&nbsp;&nbsp;
                    <div style="width: 88%;font-size:0.36rem;line-height: 0.48rem;">
                      <el-input
                      v-if="isChange === 0"
                      type="textarea"
                      :id=" String.fromCharCode(65 + index) + '_' + i.exerciseId "
                      v-model="item.tkOptionObj['option'+index]"
                      :autosize="{ minRows: 1}"
                      @input="sumInput(item.tkOptionObj)"
                      maxlength="50"
                      :class="selectClassSpan(index , i,item,item.tkOptionObj['option'+index])"
                      placeholder="请输入"
                      :readonly="item.done"
                      style="color:#2D2D2F;width:100%;" ></el-input>

                      <el-input
                      v-if="isChange === 1"
                      type="textarea"
                      :id=" String.fromCharCode(65 + index) + '_' + i.exerciseId "
                      v-model="tkOptionObjAnswer['option'+index]"
                      :autosize="{ minRows: 1}"
                      maxlength="50"
                      :class="selectClassSpanTwo(index , i,item,item.tkOptionObj['option'+index])"
                      placeholder="请输入"
                      :readonly="true"
                      style="color:#2D2D2F;width:100%;" ></el-input>
                    </div>
                  </div>
                </div>

                <div
                  class="showAd"
                  v-show="(item.exerciseType == '0102' || item.exerciseType == '0104' || item.exerciseType == '0105') && isChange == 0 && !item.done "
                >
                  <div @click="getPd(item)" :class="item.isShowOkBut?'text-mian-btn-selectmore-sure':'text-mian-btn-selectmore'">
                    <div style="color:#FFFFFF">确定</div>
                  </div>
                </div>
                <div v-show="item.done || isChange == 1">
                  <div class="balckMess"></div>
                  <div style="padding:0 0.35rem">
                    <div style="color:#333333;font-size:0.32rem;margin-top: 0.35rem;">试题解析</div>
                    <div style="margin-top: 0.17rem;margin-bottom: 0.29rem;">
                      <pre class="balckMess-dn">答案：{{item.ansString}}</pre>
                      <div class="balckMess-cont">{{item.analysis?item.analysis.wordAnswer:''}}</div>
                      <div class="balckMess-bottom">
                        <div style="display:flex;align-items:center">错误率：{{item.errorRate}}%</div>
                        <div
                          style="display:flex;align-items: center;"
                          v-if="item.complexity  == '0601'"
                        >
                          难易程度
                          <img
                            src="../../assets/image/难度1.png"
                            style="margin-left:0.1rem;width:0.32rem;height:0.32rem;margin-bottom:0.04rem"
                          />
                          <img
                            src="../../assets/image/难度2.png"
                            style="margin-left:0.1rem;width:0.32rem;height:0.32rem;margin-bottom:0.04rem"
                          />
                          <img
                            src="../../assets/image/难度2.png"
                            style="margin-left:0.1rem;width:0.32rem;height:0.32rem;margin-bottom:0.04rem"
                          />
                        </div>
                        <div
                          style="display:flex;align-items: center;"
                          v-if="item.complexity  == '0602'"
                        >
                          难易程度
                          <img
                            src="../../assets/image/难度1.png"
                            style="margin-left:0.1rem;width:0.32rem;height:0.32rem;margin-bottom:0.04rem"
                          />
                          <img
                            src="../../assets/image/难度1.png"
                            style="margin-left:0.1rem;width:0.32rem;height:0.32rem;margin-bottom:0.04rem"
                          />
                          <img
                            src="../../assets/image/难度2.png"
                            style="margin-left:0.1rem;width:0.32rem;height:0.32rem;margin-bottom:0.04rem"
                          />
                        </div>
                        <div
                          style="display:flex;align-items: center;"
                          v-if="item.complexity  == '0603'"
                        >
                          难易程度
                          <img
                            src="../../assets/image/难度1.png"
                            style="margin-left:0.1rem;width:0.32rem;height:0.32rem;margin-bottom:0.04rem"
                          />
                          <img
                            src="../../assets/image/难度1.png"
                            style="margin-left:0.1rem;width:0.32rem;height:0.32rem;margin-bottom:0.04rem"
                          />
                          <img
                            src="../../assets/image/难度1.png"
                            style="margin-left:0.1rem;width:0.32rem;height:0.32rem;margin-bottom:0.04rem"
                          />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div class="bottom-mes">
                <div @click="bjAdd(item)" class="bottom-mes-colect">
                  <div
                    :class="item.isCollect !== '1' ?'bottom-mes-mes-1-nocollect' : 'bottom-mes-mes-1-collect'"
                    class="bottom-mes-mes-0"
                  ></div>
                  <div
                    style="color:rgba(102,102,102,1);font-size:0.28rem;margin-left:0.09rem;display:flex;align-items: center;height:0.3rem;margin-top:0.03rem;"
                  >收藏</div>
                </div>
                <!-- 答题卡 -->
                <div class="bottom-mes-mes-dtk">
                  <div class="bottom-mes-mes-dtk-2">
                    <div class="bottom-mes-mes-2"></div>
                    <div class="bottom-mes-mes-2-2">{{selectList.length}}</div>
                  </div>
                  <div class="bottom-mes-mes-dtk-3">
                    <div class="bottom-mes-mes-3"></div>
                    <div class="bottom-mes-mes-3-3">{{overErrorDataList.length}}</div>
                  </div>
                  <div @click="showDTK(item.exerciseNumber)" class="bottom-mes-mes-dtk-4">
                    <div class="bottom-mes-mes-5">
                      <span style="color: #333;">{{item.exerciseNumber}}</span>
                      <span style="color:#999999">/</span>
                      <span style="color: #666;">{{allCount}}</span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- 答题卡 -->
      <div class="bigBox-dtk" v-if="isShowDTK">
        <div @click="closeDtkBox()" class="dkt-head"></div>
        <div class="dkt-message">
          <div class="dkt-message-title">
            <div class="bottom-mes-colect">
              <div
                class="bottom-mes-mes-0"
                :class="allData[testIndex - 1].isCollect === '0'?'bottom-mes-mes-1-nocollect':'bottom-mes-mes-1-collect'"
              ></div>
              <div
                style="color:rgba(102,102,102,1);font-size:0.28rem;margin-left:0.09rem;margin-top:0.03rem"
              >收藏</div>
            </div>
            <div class="bottom-mes-mes-dtk">
              <div class="bottom-mes-mes-dtk-2">
                <div class="bottom-mes-mes-2"></div>
                <div class="bottom-mes-mes-2-2">{{selectList.length}}</div>
              </div>
              <div class="bottom-mes-mes-dtk-3">
                <div class="bottom-mes-mes-3"></div>
                <div class="bottom-mes-mes-3-3">{{overErrorDataList.length}}</div>
              </div>
              <div class="bottom-mes-mes-dtk-4" id="closeDtk">
                <div class="bottom-mes-mes-4"></div>
                <div class="bottom-mes-mes-5">
                  <span style="color:#333;" id="now-page">{{testIndex}}</span>
                  <span style="color:#999999;">/</span>
                  <span style="color: #666;">{{allData.length}}</span>
                </div>
              </div>
            </div>
          </div>
          <div class="dkt-message-content">
            <div v-for="(i , index) in allData" :key="i.id" class="dtk-all">
              <div
                :id="index+1"
                class="add-list-all add-list-${i + 1}"
                :class="selectIsSelected(index+1)"
                @click="goTo(index+1)"
              >{{index+1}}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!--底下的导航栏-->

    <!-- 暂无内容 -->
    <div class="notData-all">
      <div class="body-mes">
        <div class="text-wait-font-mes">
          <img alt class="notData" src="../../assets/image/新空拷贝.png" />
        </div>
        <div class="text-wait-font-mes-two">暂无内容</div>
      </div>
    </div>
    <!-- 加载页面 -->
    <Loading :dialogVisible="verDialogVisible"></Loading>
    <!-- 无网络 -->
    <network :dialogVisible="networkDialogVisible" :mess="networkmess"></network>
  </div>
</template>

<script>
import API from '@/api/api'
import $ from 'jquery'
import { Swiper, swiperSlide } from 'vue-awesome-swiper'
import 'swiper/dist/css/swiper.css'
import Loading from './../components/loading'
import network from './../components/network'
import { async } from 'q'
import { log } from 'util'
var mySwiper = ''
let flage = true

export default {
  name: 'my',
  components: {
    Swiper,
    swiperSlide,
    Loading,
    network
  },
  data () {
    return {
      aa: '',
      parentTest: 1,
      all: [{ title: '答题模式' }, { title: '背题模式' }],
      isChange: 0, // 模式
      networkDialogVisible: false, // 无网络弹窗
      networkmess: '无网络连接，请查看网络', // 无网络提示语
      onLine: navigator.onLine,
      selectList: [], // 答对了的题号
      rightItemIds: [], // 做对了的题的id集合
      overErrorDataList: [], // 做过的错误的试题集合
      pageIndex: 0, // 当前页面的下标
      collectionList: [], // 收藏集合
      noCollectionList: [], // 未收藏集合
      errorItemList: [], // 做错题的集合
      isShowDTK: false, // 是否显示答题卡
      allCount: 0,
      activeItems: [],
      allData: [],
      testIndex: 1, // 当前页号
      verDialogVisible: false,
      isGoToNext: true, // 做对了是否可以自动跳到下一题
      errorNum: 0, // 错误题数
      rightNum: 0, // 正确题数
      totalNum: 0, // 做过的题数
      tkOptionObj: {}, // 填空题(答题模式)
      tkOptionObjAnswer: {}, // 填空题(背题模式)
      optionLength: 0, // 选项的长度
      isShowOkBut: false // 是否显示确定按钮
    }
  },
  computed: {},
  methods: {
    sumInput (item) {
      let num = 0
      for (let i = 0; i < this.optionLength; i++) {
        if (item['option' + i] !== '' && item['option' + i] !== null && item['option' + i] !== undefined) {
          num++
        }
      }
      this.isShowOkBut = num !== 0
      this.allData[this.testIndex-1].isShowOkBut = num !== 0;
    },
    // 做题积分接口
    addSelfCredits () {
      API.post(
        'addSelfCredits',
        {
          creditsId: 'exePratice',
          specialCode: this.specialCode
        },
        'json',
        (data, err) => {
          if (data.success) {
          }
        }
      )
    },
    // 物理返回键
    onBackKeyDown () {
      if (this.isShowDTK) {
        this.isShowDTK = false
      } else {
        this.back()
      }
    },
    /**
     * 跳转到第几页
     */
    async goTo (page) {
      this.testIndex = page
      if (page === 2) {
        flage = false
      } else {
        flage = true
      }
      this.getActiveItem(page)
      this.isShowDTK = false
    },
    /**
     * 样式标记
     */
    selectIsSelected (index) {
      return this.overErrorDataList.indexOf(index) !== -1
        ? 'dtk-bj-now'
        : '' || this.selectList.indexOf(index) !== -1
          ? 'dtk-select-now'
          : '' || this.testIndex === index
            ? 'dtk-pageIndex'
            : ''
    },
    // 关闭答题卡的背景图
    closeDtkBox () {
      this.isShowDTK = false
    },
    // 显示答题卡
    showDTK (item) {
      this.isShowDTK = true
    },
    // 点击收藏
    bjAdd (item) {
      let isCollectMes = 0
      if (item.isCollect !== '1') {
        item.isCollect = '1'
        isCollectMes = '1'
      } else {
        item.isCollect = '0'
        isCollectMes = '-1'
      }
      if (item.isCollect === '1') {
        this.collectionList.push(item.id)
        this.noCollectionList.splice(this.noCollectionList.indexOf(item.id), 1)
      } else {
        this.noCollectionList.push(item.id)
        this.collectionList.splice(this.noCollectionList.indexOf(item.id), 1)
      }
      this.hitCollect(item.id, isCollectMes)
    },
    // 左上角的返回按钮
    back () {
      if (this.practiceStatus === 1) {
        this.randomBack()
      } else {
        this.$router.back()
      }
    },
    deleteSingleError (id) {
      API.post(
        'deleteSingleError',
        {
          exerciseId: id
        },
        'json',
        (data, err) => {
          if (!data.success) {
            this.$toast(data.msg)
          }
        }
      )
    },
    // 顺序练习
    randomBack () {
      if (this.allData.length !== 0) {
        this.updateLastExe()
      } else {
        this.$router.back()
      }
    },
    /**
     * 点击收藏或者取消收藏
     */
    hitCollect (id, flag) {
      API.post(
        'collectSingle',
        {
          exerciseId: id,
          flag: flag
        },
        'json',
        (data, err) => {
          if (!data.success) {
            this.$toast(data.msg)
          }
        }
      )
    },
    /**
     * 顺序练习的返回阅读进度
     */
    updateLastExe () {
      const that = this
      API.post(
        'updateLastExe',
        {
          exerciseId: this.allData[that.testIndex - 1].id,
          specialType: this.specialCode,
          exerciseNo: this.testIndex
        },
        'json',
        (data, err) => {
          if (!data.success) {
            this.$router.back()
            this.$toast(data.msg)
            return
          }
          if (data.success) {
            this.$router.back()
          }
        }
      )
    },
    /**
     * 多选题或者不定向的确定按钮
     */
    getPd (item) {
      if (!item.isShowOkBut) {
        return
      }
      item.isShowOkBut = false
      item.done = true
      if (item.exerciseType === '0105') {
        let num = 0
        for (let i = 0; i < item.exerciseOptions.length; i++) {
          this.tkOptionObjAnswer['option' + i] = item.exerciseOptions[i].exerciseOption
          if (item.tkOptionObj['option' + i] !== item.exerciseOptions[i].exerciseOption) {
            num++
          }
        }
        if (num === 0) {
          this.selectList.push(this.testIndex)
          this.rightItemIds.push(item.id)
          if (this.practiceStatus === 0) {
            this.deleteSingleError(item.id)
          }
          item.isRight = '1'
          let thisIndex = Number(this.testIndex) + 1
          if (thisIndex <= this.allData.length) {
            setTimeout(() => {
              if (this.isGoToNext) {
                this.goTo(thisIndex)
              }
            }, 1000)
          }
        } else {
          this.errorItemList.push(item.id)
          item.isRight = '0'
          this.overErrorDataList.push(this.testIndex)
        }
        this.saveItem(item)
        return
      }
      let isError = item.exerciseOptions.filter(
        i =>
          (i.isCorrect === '0' && i.selected) || (i.isCorrect === '1' && !i.selected)
      )
      if (isError.length > 0) {
        this.errorItemList.push(item.id)
        item.isRight = '0'
        this.overErrorDataList.push(this.testIndex)
      } else {
        this.selectList.push(this.testIndex)
        this.rightItemIds.push(item.id)
        if (this.practiceStatus === 0) {
          this.deleteSingleError(item.id)
        }
        item.isRight = '1'
        let thisIndex = Number(this.testIndex) + 1
        if (thisIndex <= this.allData.length) {
          setTimeout(() => {
            if (this.isGoToNext) {
              this.goTo(thisIndex)
            }
          }, 1000)
        }
      }
      this.saveItem(item)
    },
    selectClassSpanTwo (index, i, item, tk) {
      let arr = []
      for (let j = 0; j < item.exerciseOptions.length; j++) {
        this.tkOptionObjAnswer['option' + j] = item.exerciseOptions[j].exerciseOption
      }
      arr.push(`tk-right`)
      return arr
    },
    selectClassSpan (index, i, item, tk) {
      let arr = []
      // 判断题或者单选题
      if (item.exerciseType == '0101' || item.exerciseType == '0103') {
        // 答题模式
        if (this.isChange === 0) {
          // 没有选择的正确选项
          if (i.isCorrect === '1' && item.checked.length != 0) {
            arr.push(`right`)
          }
          // 选择了的错误选项
          if (i.isCorrect !== '1' && item.checked.indexOf(i.id) !== -1) {
            arr.push(`error`)
          }
        } else {
          // 背题模式
          if (this.isChange === 1 && i.isCorrect === '1') {
            arr.push(`right`)
          }
        }
      } else if (item.exerciseType == '0105') {
        if (item.done === true) {
          if (this.isChange === 0) {
            if (tk === i.exerciseOption) {
              arr.push(`tk-right`)
            } else {
              arr.push(`tk-error`)
            }
          }
        } else if (this.isChange === 1) {
          for (let j = 0; j < item.exerciseOptions.length; j++) {
            this.tkOptionObjAnswer['option' + j] = item.exerciseOptions[j].exerciseOption
          }
          arr.push(`tk-right`)
        }
      } else {
        // 答题模式
        if (this.isChange === 0) {
          if (!item.done) {
            if (i.selected && item.checked.length != 0) {
              arr.push(`right`)
            }
          } else {
            // 不是正确答案并且选择了
            if (i.isCorrect === '0' && item.checked.indexOf(i.id) !== -1) {
              arr.push(`error`)
            }
            // 是正确答案并且没选择了
            if (i.isCorrect === '1' && item.checked.indexOf(i.id) === -1) {
              arr.push(`right`)
            }
            // 是正确答案并且选择了
            if (i.isCorrect === '1' && item.checked.indexOf(i.id) !== -1) {
              arr.push(`right`)
            }
          }
        } else {
          // 是正确答案
          if (i.isCorrect === '1') {
            arr.push(`right`)
          }
        }
      }
      return arr
    },
    selectClass (index, i, item) {
      let arr = [String.fromCharCode(65 + index)]
      // 判断题或者单选题
      if (item.exerciseType == '0101' || item.exerciseType == '0103') {
        // 答题模式
        if (this.isChange === 0) {
          // 没有选择的正确选项
          if (i.isCorrect === '1' && item.checked.length != 0) {
            arr.push(`${String.fromCharCode(65 + index)}-get`)
          }
          // 选择了的错误选项
          if (i.isCorrect !== '1' && item.checked.indexOf(i.id) !== -1) {
            arr.push(`${String.fromCharCode(65 + index)}-eor`)
          }
        } else {
          // 背题模式
          if (this.isChange === 1 && i.isCorrect === '1') {
            arr.push(`${String.fromCharCode(65 + index)}-get`)
          }
        }
      } else {
        // 答题模式
        if (this.isChange === 0) {
          if (!item.done) {
            if (i.selected && item.checked.length != 0) {
              arr.push(`${String.fromCharCode(65 + index)}-get`)
            }
          } else {
            // 不是正确答案并且选择了
            if (i.isCorrect === '0' && item.checked.indexOf(i.id) !== -1) {
              arr.push(`${String.fromCharCode(65 + index)}-eor`)
            }
            // 是正确答案并且没选择了
            if (i.isCorrect === '1' && item.checked.indexOf(i.id) === -1) {
              arr.push(`${String.fromCharCode(65 + index)}-getmore`)
            }
            // 是正确答案并且选择了
            if (i.isCorrect === '1' && item.checked.indexOf(i.id) !== -1) {
              arr.push(`${String.fromCharCode(65 + index)}-get`)
            }
          }
        } else {
          // 是正确答案
          if (i.isCorrect === '1') {
            arr.push(`${String.fromCharCode(65 + index)}-getmore`)
          }
        }
      }
      return arr
    },
    // 调用接口获取数据(获取所有的id)
    async getDataList (url) {
      await API.post(
        url,
        {
          specialCode: this.specialCode,
          practiceStatus: this.practiceStatus
        },
        'json',
        async data => {
          if (!data.success) {
            alert(data.msg)
            return
          }
          if (data.success) {
            this.allData = data.data
            this.activeItems = [...this.allData].splice(0, 3)
            if (data.data.length == 0) {
              $('.notData-all').show()
              $('.bottom-mes').hide()
            } else {
              $('.notData-all').hide()
              this.allCount = data.data.length
            }
            if(this.practiceStatus === 1){
              if(this.allData.length !== 0){
                this.findExeNumber ()
              } 
            }else if(this.practiceStatus === 2){
              if (this.testIndex === 1) {
                this.initSwiper(0)
              } else if (this.testIndex === this.allData.length) {
                this.initSwiper(2)
              } else {
                this.initSwiper(1)
              }
              this.getActiveItem(this.testIndex)
            }
          }
        }
      )
    },
    // 顺序练习上次看到的题号
    async findExeNumber () {
      API.post(
        'findExeNumber',
        {
          specialType: this.specialCode
        },
        'json',
        (data, err) => {
          if (data.success) {
            this.isRefresh = data.data.isRefresh
            if (this.isRefresh !== '1') {
              
            } else {
              this.$toast('题库存在更新,将重新开始答题!')
              this.testIndex = 1
            }
            if (this.testIndex === 1) {
              this.initSwiper(0)
            } else if (this.testIndex === this.allData.length) {
              this.initSwiper(2)
            } else {
              this.initSwiper(1)
            }
            this.getActiveItem(this.testIndex)
          } else {
            this.$toast(data.msg)
          }
        }
      )
    },
    async sequenPratice (item) {
      if (item > this.allData.length) {
        return
      }
      const that = this
      let isData = {}
      await API.post(
        'sequenPratice',
        {
          id: this.allData[item - 1],
          exerciseNumber: Number(item),
          flag:'1'
        },
        'json',
        async data => {
          if (!data.success) {
            alert(data.msg)
            return
          }
          if (data.success) {
            data.data.tkOptionObj = {}
            data.data.isShowOkBut = false
            data.data.checked = []
            if (data.data.isCollect === null) {
              data.data.isCollect = 0
            }
            if (that.allData[item - 1].isCollect !== null && that.allData[item - 1].isCollect !== undefined) {
              data.data.isCollect = that.allData[item - 1].isCollect
            }
            isData = data.data
            data.data.done = that.allData[item - 1].done
            if (
              that.allData[item - 1].stem == null ||
              that.allData[item - 1].stem == null
            ) {
              that.allData.splice(item - 1, 1, data.data)
            }
            that.allData[item - 1].ansString = ''
            that.optionLength = that.allData[item - 1].exerciseOptions.length
            for (
              let ii = 0;
              ii < that.allData[item - 1].exerciseOptions.length;
              ii++
            ) {
              if (that.allData[item - 1].exerciseType === '0105') {
                that.allData[item - 1].ansString += that.allData[item - 1].exerciseOptions[ii].exerciseOption + '   '
              } else {
                if (that.allData[item - 1].exerciseOptions[ii].isCorrect == 1) {
                  that.allData[item - 1].ansString += `${String.fromCharCode(
                    65 + ii
                  )} `
                }
              }
            }
            that.showAllMess = true
            setTimeout(() => {
              $('#swiperId').removeClass('swiper-no-swiping')
            }, 0.1)
          }
        }
      )
      return isData
    },
    // 初始化
    initSwiper (showIndex) {
      const that = this
      let status = ''
      that.isCanGo = true
      if (showIndex === 0) {
        flage = false
      }
      mySwiper = new Swiper('.swiper-container', {
        observer: true, // 修改swiper自己或子元素时，自动初始化swiper
        observeParents: true, // 修改swiper的父元素时，自动初始化swiper
        longSwipesRatio: 0.1,
        initialSlide: showIndex,
        disableOnInteraction: false, // 解决滑动后不能轮播的问题
        on: {
          sliderMove () {},
          async slideChange () {
            this.isGoToNext = false
            flage = !flage
            if (!flage) {
              return
            }
            if (that.testIndex === 1 && mySwiper.activeIndex === 0) {
              flage = false
            }
            if (status === 'next' && flage) {
              that.testIndex++
              if (that.allData.length === 2) {
                return
              }
              if (that.testIndex > that.allData.length) {
                that.testIndex = that.allData.length
                return
              }

              if (that.testIndex === that.allData.length) {
                // flage = false
                await that.getActiveItem(that.testIndex)
                return
              }
              that.getActiveItem(that.testIndex)
            } else if (status === 'prev' && flage) {
              that.testIndex--
              if (that.testIndex > 1) {
                that.getActiveItem(that.testIndex)
              } else {
                that.activeItems.splice(0, 1, that.allData[0])
                mySwiper.slideTo(0, 0, false)
                that.testIndex = 1
                that.pageIndex = 0
              }
            }
          },
          slideChangeTransitionEnd () {
            this.isGoToNext = false
            if (mySwiper) {
              that.pageIndex = mySwiper.activeIndex
              if (mySwiper.activeIndex === 0) {
                if (that.testIndex !== 1) {
                  mySwiper.slideTo(1, 0, false)
                } else {
                  flage = false
                }
              }
              if (mySwiper.activeIndex === 1) {
                flage = false
              }
              if (mySwiper.activeIndex === 2) {
                if (that.allData.length === 2) {
                  that.testIndex = 2
                  return
                }
                if (that.testIndex === that.allData.length) {
                  that.testIndex = that.allData.length
                  flage = false
                  return
                }
                if (!that.allData[that.testIndex].stem) {
                  that.activeItems.splice(2, 1, {})
                }
                mySwiper.slideTo(1, 0, false)
              }
            }
          },
          touchEnd (event) {
            status = mySwiper.swipeDirection
          }
        }
      })
    },
    // 选择答案
    select (item, i) {
      if (this.allData[this.testIndex - 1].done || this.isChange === 1) {
        return
      }
      if (item.exerciseType === '0101' || item.exerciseType === '0103') {
        this.allData[this.testIndex - 1].done = true // 标记该题已做
        item.checked = []
        i.selected = true
        item.checked.push(i.id)
        if (i.isCorrect === '1') {
          if (this.practiceStatus === 0) {
            this.deleteSingleError(item.id)
          }
          this.selectList.push(this.testIndex)
          this.rightItemIds.push(item.id)
          item.isRight = '1'
          let thisIndex = Number(this.testIndex) + 1
          if (thisIndex <= this.allData.length) {
            setTimeout(() => {
              if (this.isGoToNext) {
                this.goTo(thisIndex)
              }
            }, 1000)
          }
        } else {
          item.isRight = '0'
          this.overErrorDataList.push(this.testIndex)
          this.errorItemList.push(item.id)
        }
        this.saveItem(item)
      } else {
        i.selected = !i.selected
        if (i.selected) {
          item.checked.push(i.id)
          item.isShowOkBut = true
        } else {
          item.checked.splice(item.checked.indexOf(i.id), 1)
          if (item.checked.length === 0) {
            item.isShowOkBut = false
          }
        }
      }
    },
    saveItem (item) {
      let optionBackList = []
      for (let index = 0; index < item.exerciseOptions.length; index++) {
        let optionObj = {}
        optionObj.exerciseOption = item.exerciseOptions[index].exerciseOption
        optionObj.isCorrect = item.exerciseOptions[index].isCorrect
        optionObj.sortField = item.exerciseOptions[index].sortField
        if (item.exerciseType === '0105') {
          optionObj.answer = item.tkOptionObj['option' + index ]
        } else {
          optionObj.isSelected = item.exerciseOptions[index].selected ? '1' : '0'
        }
        optionBackList.push(optionObj)
      }
      let analysisObj = { ...item.analysis }
      analysisObj.id = null
      let obj = {
        exerciseId: item.id,
        specialType: item.specialType,
        exerciseType: item.exerciseType,
        stem: item.stem,
        complexity: item.complexity,
        itemFlag: '2',
        md5: item.md5,
        isRight: item.isRight,
        optionItems: optionBackList,
        analysis: analysisObj
      }
      API.post(
        'saveItem',
        obj,
        'json',
        async data => {
          if (!data.success) {
            alert(data.msg)
            return
          }
          if (data.success) {

          }
        }
      )
    },
    // 点击答题模式和背题模式
    go (index) {
      if (this.allData.length === 0) {
        this.isChange = index
      } else if (this.isCanGo) {
        this.isChange = index
      }
    },
    // 当前页
    async getActiveItem (idx) {
     if (this.allData.length === 1) {
         if (!this.allData[0].stem) {
           let isDataLeft = await this.sequenPratice(Number(idx))
            this.activeItems.splice(0, 1, isDataLeft)
         }
        return
      }
      if (this.allData.length === 0) {
        return
      }

      if (idx === 1) {
        // 判断第二页有无数据

        if (!this.allData[idx].stem) {
          let isData = await this.sequenPratice(Number(idx) + 1)
          this.activeItems.splice(1, 1, isData)
        } else {
          this.activeItems.splice(1, 1, this.allData[idx])
        }
        // 判断当前页有无数据
        if (!this.allData[idx - 1].stem) {
          let isDataLeft = await this.sequenPratice(Number(idx))
          this.activeItems.splice(0, 1, isDataLeft)
        } else {
          this.activeItems.splice(0, 1, this.allData[idx - 1])
        }
        mySwiper.slideTo(0, 0, false)
      } else if (idx === this.allData.length) {
        if (this.allData.length === 2 && idx === 2) {
          // 前一页有无数据
          if (!this.allData[idx - 2].stem) {
            let isData = await this.sequenPratice(Number(idx) - 1)
            this.activeItems.splice(0, 1, isData)
          } else {
            this.activeItems.splice(0, 1, this.allData[idx - 2])
          }
          // 判断当期页有无数据
          if (!this.allData[idx - 1].stem) {
            let isDataRight = await this.sequenPratice(Number(idx))
            this.activeItems.splice(1, 1, isDataRight)
          } else {
            this.activeItems.splice(1, 1, this.allData[idx - 1])
          }
          mySwiper.slideTo(2, 0, false)
          return
        }
        // 前一页有无数据
        if (!this.allData[idx - 2].stem) {
          let isData = await this.sequenPratice(Number(idx) - 1)
          this.activeItems.splice(1, 1, isData)
        } else {
          this.activeItems.splice(1, 1, this.allData[idx - 2])
        }
        // 判断当期页有无数据
        if (!this.allData[idx - 1].stem) {
          let isDataRight = await this.sequenPratice(Number(idx))
          this.activeItems.splice(2, 1, isDataRight)
        } else {
          this.activeItems.splice(2, 1, this.allData[idx - 1])
        }
        mySwiper.slideTo(2, 0, false)
        // mySwiper.slideTo(2, 0, false)
      } else {
        // 判断前一页
        // 前一页有无数据
        if (!this.allData[idx - 2].stem) {
          let leftData = await this.sequenPratice(Number(idx) - 1)
          this.activeItems.splice(0, 1, leftData)
        } else {
          this.activeItems.splice(0, 1, this.allData[idx - 2])
        }
        // 判断后一页
        if (!this.allData[idx].stem) {
          let rightData = await this.sequenPratice(Number(idx) + 1)
          this.activeItems.splice(2, 1, rightData)
        } else {
          this.activeItems.splice(2, 1, this.allData[idx])
        }
        // 判断当期页有无数据
        if (!this.allData[idx - 1].stem) {
          let isData = await this.sequenPratice(Number(idx))
          this.activeItems.splice(1, 1, isData)
        } else {
          this.activeItems.splice(1, 1, this.allData[idx - 1])
        }
        mySwiper.slideTo(1, 0, false)
      }
    }
  },

  // 消除返回键监听
  beforeDestroy () {
    // window.removeEventListener('online', this.updateOnlineStatus)
    // window.removeEventListener('offline', this.updateOnlineStatus)
    document.removeEventListener('backbutton', this.onBackKeyDown, false)
  },
  watch: {
    errorItemList (val) {
      this.errorNum = val.length
      this.totalNum = Number(this.errorNum) + Number(this.rightNum)
      if (this.totalNum !== 0) {
        if (this.totalNum % 20 === 0) {
          this.addSelfCredits()
        }
      }
    },
    rightItemIds (val) {
      this.rightNum = val.length
      this.totalNum = Number(this.errorNum) + Number(this.rightNum)
      if (this.totalNum !== 0) {
        if (this.totalNum % 20 === 0) {
          this.addSelfCredits()
        }
      }
    }
  },

  mounted () {
    // window.addEventListener('online', this.updateOnlineStatus)
    // window.addEventListener('offline', this.updateOnlineStatus)
    $('.notData-all').hide()

    // 监听返回键
    document.addEventListener('backbutton', this.onBackKeyDown, false)
  }
}
</script>

