
<template>
  <el-dialog
    class="loading"
    :visible.sync="dialogVisible"
    :modal-append-to-body="false"
    :close-on-click-modal="false"
    top="0"
    :center="true"
    :show-close="false">
    <div class="body-mes">
      <!-- <div class="font-mes">
        LOADING
      </div>
      <div class="font-mes-two">
        Please wait...
      </div> -->
      <div class="font-mes">
        <img src="../../assets/image/加载中.gif" alt="">
      </div>
      <div class="font-mes-two">加载中...</div>
    </div>
  </el-dialog>
</template>

<script>
export default {
  props: ['dialogVisible']
}
</script>

<style lang="less">
  .loading .el-dialog__wrapper{
    position: absolute !important;
    top:0 !important;
    left:0;
  }
  .loading .el-dialog{
    position: relative;
    width:100%;
    height:100%;
    background: white;
    margin:0 !important;
  }
  .loading .el-dialog__header{
    height: 0;
  }
  .loading{
    width:100%;
    height:100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    top:0;
    left:0;
  }
  .loading .el-dialog__body{
    position: absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .loading .body-mes{
    width: 100%;
    font-size:0.66rem;
    color:rgba(25,180,143,1);
    // margin-bottom: 0.43rem;
    font-weight: bold;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .loading .font-mes{
    width:0.66rem;
    height:0.66rem;
  }
  .loading .font-mes-two{
    margin-top:0.4rem;
    width: 100%;
    font-size: 0.32rem;
    color:#6E747E;
    display: flex;
    justify-content: center;
    font-weight: normal;
   }
  .loading .font-mes img{
    width:0.66rem;
    height:0.66rem;
    display: block;
   }
</style>



