
<template>
  <div
    class="itemLoading" v-show="dialogVisible"
    >
    <div class="body-mes">
      <!-- <div class="font-mes">
        itemLoading
      </div>
      <div class="font-mes-two">
        Please wait...
      </div> -->
      <div class="font-mes">
        <img src="../../assets/image/加载中.gif" alt="">
      </div>
      <div class="font-mes-two">加载中...</div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['dialogVisible']
}
</script>

<style lang="less">
  .itemLoading .el-dialog__wrapper{
    position: absolute !important;
    top:0 !important;
    left:0;
    width:100%;
    height:calc(100vh - 0.98rem);
  }
  .itemLoading .el-dialog{
    position: relative;
    width:100%;
    height:calc(100vh - 0.98rem);
    background: white;
    margin:0 !important;
  }
  .itemLoading .el-dialog__header{
    height: 0;
  }
  .itemLoading{
    background: white;
    width:100%;
    height:calc(100vh - 0.98rem);
    display: flex;
    justify-content: center;
    position: fixed;
    top:0;
    left:0;
    z-index: 2000;
  }
  .itemLoading .el-dialog__body{
    position: absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .itemLoading .body-mes{
    width: 100%;
    font-size:0.66rem;
    color:rgba(25,180,143,1);
    // margin-bottom: 0.43rem;
    font-weight: bold;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .itemLoading .font-mes{
    width:0.66rem;
    height:0.66rem;
  }
  .itemLoading .font-mes-two{
    margin-top:0.4rem;
    width: 100%;
    font-size: 0.32rem;
    color:#6E747E;
    display: flex;
    justify-content: center;
    font-weight: normal;
   }
  .itemLoading .font-mes img{
    width:0.66rem;
    height:0.66rem;
    display: block;
   }
</style>



