<template>
  <el-dialog
    class="network"
    :visible.sync="dialogVisible"
    :modal-append-to-body="false"
    :close-on-click-modal="false"
    top="0"
    :center="true"
    :show-close="false">
    <div class="dialog-body-img"><img src="../../assets/image/开始考试的背景.png" alt=""></div>
    <div class="dialog-body-text"><span>{{mess}}</span></div>
    <!-- <div slot="footer" class="dialog-footer" style="" @click="sure">
      <span >确定</span>
    </div> -->
  </el-dialog>
</template>

<script>
export default {
  props: ['dialogVisible', 'mess'],

  methods: {
    sure () {
      this.$emit('inNesclose')
    }
  }
}
</script>

<style lang="less">
  .network .el-dialog__wrapper{
    position: absolute !important;
    top:0 !important;
    left:0;
    // background: #fff;
    // width:calc(100vw - 2rem);
    // padding:0 1rem;
  }
  .network .el-dialog{
    position: relative;
    width:5.5rem;
    height:2.74rem;
    margin:0;
    border-radius: 0.5rem;
  }
  .network .dialog-body-img{
    width:5.5rem;
    height:2.74rem;
    display: flex;
    align-items: center;
    justify-content: center;
    position:absolute;
    left:0;
    top:0;
  }
  .network .el-dialog__header{
    padding:0;
  }
  .network .dialog-body-img img{
    text-align: center;
    width:5.5rem;
    height:2.74rem;
  }
  .network{
    width:100%;
    height:100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top:0;
    left:0;
  }
  .network .el-dialog__body{
    position: relative;
    width:5.5rem;
    height:2.74rem;
    text-align: center
  }
  // .dialog-body-title{
  //   font-size:0.32rem;
  // }
  .network .dialog-body-text{
    position: absolute;
    width:100%;
    height:100%;
    font-size:0.34rem;
    top:0;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-weight:bold;
    left:0;
  }
  .network .dialog-body-text span{
    width:4.8rem;
    display: inline-block;
    text-align: center;
    color:#4E4E4E;
    letter-spacing:0.04rem;
  }
  .network .el-dialog__footer{
    position: absolute;
    bottom:0;
    left:0;
    width:100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding:0;
    height: 1rem;
  }
  .network .dialog-footer{
    width:100%;
    height: 1rem;
    border-top: 0.012rem solid #E7ECF4;
  }
  .network .dialog-footer div{
    width:100%;
    height:100%;
    position: relative;
  }
  .network .dialog-footer span{
    display: flex;
    justify-content: center;
    align-items: center;
    color:#5794F6;
    font-size:0.3rem;
    height: 1rem;
  }
</style>



